.education-container {
    padding: 40px;
    background-color: #e3e3e3;
  }
  
  .education-heading {
    font-size: 40px;
    font-weight: bold;
    text-align: center;
    margin-bottom: 20px;
    position: relative;
  }
  
  .education-icon {
    width: 60px;
    margin-left: 10px;
    vertical-align: middle;
  }
  
  .education-list {
    display: flex;
    flex-direction: column;
    margin-left: 330px;
    gap: 20px;
    width: 1000px;
  }
  .skills-divider {
    border: 0;
    height: 5px;
    background: #007bff;
    width: 60%;
    margin: 0 auto 30px;
  }
  .education-item {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 20px;
    border: 2px solid #0000ff;
    border-radius: 10px;
  }
  
  .education-details {
    display: flex;
    align-items: center;
    gap: 15px;
  }
  
  .bullet-point {
    width: 12px;
    height: 12px;
    background-color: #0000ff;
    border-radius: 50%;
    flex-shrink: 0;
  }
  
  .education-year {
    font-size: 18px;
    font-weight: bold;
    color: #ffffff;
    background-color: #0000ff;
    padding: 10px 20px;
    border-radius: 20px;
  }
  
  h3 {
    font-size: 20px;
    color: #3c0f99;
    margin: 0;
  }
  
  .sub-title {
    font-size: 16px;
    color: #f57c00;
    margin: 5px 0;
  }
  
  .score {
    font-size: 16px;
    color: #000;
    font-weight: bold;
  }
  
/* Mobile view adjustments */
@media screen and (max-width: 768px) {
  .education-list {
    margin-left: 0;
    gap: 15px;
    width: 100%;
  }

  .education-item {
    flex-direction: column;
    align-items: flex-start;
    padding: 15px;
  }

  .education-details {
    flex-direction: column;
    align-items: flex-start;
    gap: 10px;
  }

  .education-year {
    font-size: 16px;
    padding: 8px 15px;
  }

  h3 {
    font-size: 18px;
  }

  .sub-title {
    font-size: 14px;
  }

  .score {
    font-size: 14px;
  }

  .education-heading {
    font-size: 30px;
    margin-bottom: 10px;
  }
}

@media screen and (max-width: 480px) {
  .education-heading {
    font-size: 24px;
  }

  .education-year {
    font-size: 14px;
    padding: 6px 12px;
  }

  h3 {
    font-size: 16px;
  }

  .sub-title {
    font-size: 12px;
  }

  .score {
    font-size: 12px;
  }

  .education-item {
    padding: 12px;
  }

  .education-details {
    gap: 8px;
  }
}