.contact-section {
    text-align: center;
    background-color: #f4f4f4;
    padding: 20px;
    margin-top: 180px;
  }
  
  .contact-section h1 {
    font-size: 2.5rem;
    margin-bottom: 10px;
  }
  
  .contact-section p {
    font-size: 1.2rem;
    color: gray;
    margin-bottom: 20px;
  }
  
  .contact-divider {
    border: 0;
    height: 2px;
    background: #007bff;
    width: 50%;
    margin: 0 auto 30px;
  }
  
  .contact-icons {
    display: flex;
    justify-content: center;
    gap: 20px;
    margin-bottom: 20px;
  }
  
  .contact-icons img {
    width: 40px;
    height: 40px;
    cursor: pointer;
  }
  
  .contact-icons img:hover {
    transform: scale(1.1);
    transition: transform 0.3s;
  }
  
  .contact-image img {
    max-width: 100%;
    height: auto;
    border-radius: 8px;
    margin: 20px 0;
  }
  
  .contact-footer {
    background: #dcdcdc;
    color: gray;
    padding: 10px 0;
    font-size: 0.9rem;
  }
  
  .contact-footer p {
    margin: 0;
  }
  
/* Existing styles remain unchanged */

/* Add styles for mobile view */
@media screen and (max-width: 768px) {
  .contact-section {
    padding: 15px; /* Adjust padding for smaller screens */
    margin-top: 100px; /* Reduce top margin */
  }

  .contact-section h1 {
    font-size: 2rem; /* Reduce heading font size */
    margin-bottom: 10px;
  }

  .contact-section p {
    font-size: 1rem; /* Adjust paragraph font size */
    margin-bottom: 15px;
  }

  .contact-divider {
    height: 1px; /* Reduce divider height */
    width: 80%; /* Increase width for smaller screens */
    margin-bottom: 20px;
  }

  .contact-icons {
    flex-wrap: wrap; /* Allow icons to wrap */
    gap: 15px; /* Adjust spacing between icons */
  }

  .contact-icons img {
    width: 35px; /* Reduce icon size */
    height: 35px;
  }

  .contact-image img {
    width: 90%; /* Make image responsive */
    margin: 15px 0; /* Adjust margins */
  }

  .contact-footer {
    font-size: 0.8rem; /* Adjust footer text size for smaller screens */
    padding: 8px 0; /* Reduce padding for the footer */
  }

  .contact-footer p {
    margin: 0; /* Ensure consistent spacing */
    line-height: 1.4; /* Adjust line height for better readability */
  }
}
