.about {
    font-family: Arial, sans-serif;
    padding: 10px 20px;
    background-color: #dedede;
    height: 800px;
  }
  
  .container {
    max-width: 1200px;
    margin: 0 auto;
  }
  .skills-divider {
    border: 0;
    height: 5px;
    background: #007bff;
    width: 90%;
    margin: 0 auto 30px;
  }
  
  .about-heading {
    text-align: center;
    font-size: 40px;
    font-weight: bold;
    margin-bottom: 30px;
  }
  
  .about-content {
    display: flex;
    align-items: center;
    gap: 50px;
  }
  
  .about-image img {
    width: 400px;
    height: 550px;
    border-radius: 10px;
  }
  
  .about-highlights {
    flex: 1;
  }
  
  .about-highlights h3 {
    position: relative;
    bottom: 30px;
    color: #ff5722;
    font-size: 32px;
    margin-bottom: 20px;
  }
  
  .about-highlights ul {
    list-style: none;
    padding: 0;
    margin: 0 0 100px 0;
  }
  
  .about-highlights li {
    display: flex;
    align-items: center;
    font-size: 18px;
    margin-bottom: 50px;
  }
  
  .about-highlights .icon {
    font-size: 20px;
    color: #0066ff;
    margin-right: 10px;
  }
  
  .buttons {
    margin-top: 20px;
  }
  
  .btn {
    padding: 10px 20px;
    font-size: 16px;
    color: #fff;
    border: none;
    border-radius: 5px;
    cursor: pointer;
  }
  
  .hire-me {
    background-color: #0066ff;
    margin-right: 10px;
  }
  
  .resume {
    background-color: #0066ff;
  }
  /* Existing styles remain unchanged */

/* Add styles for mobile view */
@media screen and (max-width: 768px) {
  .about {
    padding: 10px;
    height: auto; /* Allow flexible height */
  }

  .container {
    width: 100%; /* Utilize full screen width */
    padding: 0 10px; /* Add padding */
  }

  .about-heading {
    font-size: 28px; /* Reduce heading size */
    margin-bottom: 20px;
  }

  .skills-divider {
    height: 3px; /* Reduce height */
    margin-bottom: 20px;
  }

  .about-content {
    flex-direction: column; /* Stack items vertically */
    align-items: center;
    gap: 20px;
  }

  .about-image img {
    width: 90%; /* Resize image */
    max-width: 300px;
    height: auto; /* Maintain aspect ratio */
    border-radius: 10px;
  }

  .about-highlights {
    text-align: center; /* Center-align text */
  }

  .about-highlights h3 {
    font-size: 24px; /* Reduce font size */
    bottom: 0; /* Remove relative position */
    margin-bottom: 15px;
  }

  .about-highlights ul {
    margin-bottom: 50px; /* Reduce bottom margin */
  }

  .about-highlights li {
    font-size: 16px; /* Reduce font size */
    margin-bottom: 15px;
  }

  .about-highlights .icon {
    font-size: 18px; /* Adjust icon size */
    margin-right: 8px;
  }

  .buttons {
    margin-top: 10px; /* Adjust button margin */
  }

  .btn {
    padding: 8px 15px; /* Adjust button padding */
    font-size: 14px; /* Reduce button font size */
  }

  .hire-me,
  .resume {
    margin: 5px 0; /* Add margin between buttons */
    width: 100%; /* Make buttons full-width */
  }
}
